.tt-row-container {
    @include clearfix();
    margin: 1rem -1rem;
}
.tt-split-container {
    @media screen and (min-width: $viewport-large) {
        box-sizing: border-box;
        float: left;
        padding: 0 1em;
        width: 50%;
        textarea {
            width: 90%;
        }
    }
}

.tt-update {
    background-color: #fafafa;
    margin: (8rem/16) 0;
    padding: (8rem/16);

    &-canonicalLink {
        display: block;
        margin: 0 0 1rem 0;
    }
    &-date {
        font-size: (14rem/16);
        margin: 0 0 0 .5em;
    }
    &-notes {
        margin: 1em 0;
    }
    &-user {
        color: #3a3a3a;
        font-size: (18rem/16);
        font-weight: normal;
    }
}
